import { Component, OnInit, Input } from '@angular/core';
import { Dish } from '../shared/dish';
import { DISHES } from '../shared/dishes';


@Component({
  selector: 'app-stone-type',
  templateUrl: './stone-type.component.html',
  styleUrls: ['./stone-type.component.css']
})
export class StoneTypeComponent implements OnInit {
  dishes: Dish[] = DISHES;

  selectedDish: Dish;

  @Input()
  dish:Dish;
  constructor() { }

  ngOnInit(): void {
  }

  onSelect(dish: Dish) {
    this.selectedDish = dish;
  }

}
