import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

import { HomeComponent } from './home/home.component';
import { GalleryComponent } from './gallery/gallery.component';
import { AboutUsComponent } from './about-us/about-us.component';
import { ContactUsComponent } from './contact-us/contact-us.component';
import { from } from 'rxjs';
import { SlateComponent } from './slate/slate.component';
import { GraniteComponent } from './granite/granite.component';
import { MarbleComponent } from './marble/marble.component';
import { SandstoneComponent } from './sandstone/sandstone.component';
import { LimestoneComponent } from './limestone/limestone.component';
import { LoginComponent } from './login/login.component';
import { StoneTypeComponent } from './stone-type/stone-type.component';
import { AdminPageComponent } from './admin/admin-page/admin-page.component';
import { AuthGuard } from './services/auth-guard.service';
import { ArticlesComponent } from './articles/articles.component';
import { AdminAuthGuard } from './admin-auth-guard.service';

const routes: Routes = [
  {path: '' , component: HomeComponent},
  {path: 'gallery' , component: GalleryComponent},
    {path: 'about-us' , component: AboutUsComponent},
    {path: 'contact-us' , component: ContactUsComponent},
    {path: 'product/slate', component: SlateComponent},
    {path: 'product/granite', component: GraniteComponent},
    {path: 'product/marble', component: MarbleComponent},
    {path: 'product/sandstone', component: SandstoneComponent},
    {path: 'product/limestone', component: LimestoneComponent},
    {path: 'product/slate/stone-type', component: StoneTypeComponent},
    {path: 'ProductUsage', component: ArticlesComponent},
    {path: 'login', component: LoginComponent},
    {path: 'admin/admin-page', component: AdminPageComponent, canActivate: [AuthGuard, AdminAuthGuard]}
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
